<template>
<div>
	<div :style='{"padding":"12px","margin":"10px auto","borderColor":"#dbd9f4","borderRadius":"8px","background":"#fff","borderWidth":"0 0 2px","width":"1200px","borderStyle":"solid"}' class="breadcrumb-preview">
		<el-breadcrumb :separator="''" :style='{"width":"100%","margin":"0 auto","fontSize":"14px","lineHeight":"1","display":"flex"}'>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item></el-breadcrumb-item>
			<el-breadcrumb-item>{{id ? '' : ''}}</el-breadcrumb-item>
		</el-breadcrumb>
	</div>
	
	<div class="add-preview" :style='{"width":"1200px","margin":"10px auto","position":"relative","background":"none"}'>
		<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm" :style='{"border":"2px solid #dbd9f4","padding":"40px 20px","borderRadius":"8px","background":"#fff"}'>
			
			<el-form-item label="" prop="shicaiName">
				<el-autocomplete
					v-model="ruleForm.shicaiName"
					:fetch-suggestions="queryShelfLife"
					placeholder=""
					@select="handleShelfLifeSelect"
					:style='{"width":"100%"}'
					clearable>
					<template slot-scope="{ item }">
						<div class="shelf-life-item">
							<span class="name">{{ item.shicaiName }}</span>
							<span class="shelf-life">: {{ item.shelfLifeDays }}</span>
						</div>
					</template>
				</el-autocomplete>
				<div v-if="selectedShelfLife" :style='{"marginTop":"8px","padding":"8px","background":"#f0f9ff","borderRadius":"4px","fontSize":"12px","color":"#409eff"}'>
					<i class="el-icon-info"></i> {{selectedShelfLife.shelfLifeDays}}
					<span v-if="selectedShelfLife.storageMethod"> | {{selectedShelfLife.storageMethod}}</span>
					<el-button type="text" size="mini" @click="applyShelfLife" :style='{"marginLeft":"10px"}'></el-button>
				</div>
			</el-form-item>
			
			<el-form-item label="" prop="quantity">
				<el-input-number v-model="ruleForm.quantity" :min="1" :max="99999" label=""></el-input-number>
			</el-form-item>
			
			<el-form-item label="" prop="unit">
				<el-select v-model="ruleForm.unit" placeholder="" clearable>
					<el-option label="" value=""></el-option>
					<el-option label="" value=""></el-option>
					<el-option label="" value=""></el-option>
					<el-option label="" value=""></el-option>
					<el-option label="" value=""></el-option>
					<el-option label="" value=""></el-option>
					<el-option label="" value=""></el-option>
					<el-option label="" value=""></el-option>
					<el-option label="" value=""></el-option>
				</el-select>
			</el-form-item>
			
			<el-form-item label="" prop="purchaseDate">
				<el-date-picker
					v-model="ruleForm.purchaseDate"
					type="datetime"
					placeholder=""
					value-format="yyyy-MM-dd HH:mm:ss"
					:style='{"width":"100%"}'>
				</el-date-picker>
			</el-form-item>
			
			<el-form-item label="" prop="expiryDate">
				<el-date-picker
					v-model="ruleForm.expiryDate"
					type="datetime"
					placeholder=""
					value-format="yyyy-MM-dd HH:mm:ss"
					:style='{"width":"100%"}'>
				</el-date-picker>
			</el-form-item>
			
			<el-form-item label="" prop="status">
				<el-radio-group v-model="ruleForm.status">
					<el-radio label="new"></el-radio>
					<el-radio label="used"></el-radio>
					<el-radio label="expired"></el-radio>
					<el-radio label="discarded"></el-radio>
				</el-radio-group>
			</el-form-item>
			
			<el-form-item>
				<el-button type="primary" @click="submitForm('ruleForm')" :style='{"cursor":"pointer","border":"0","padding":"0px 30px","margin":"0 10px 0 0","outline":"none","color":"#666","borderRadius":"6px","background":"linear-gradient(90deg, rgba(255,233,100,1) 0%, rgba(194,248,126,1) 29%, rgba(181,233,252,1) 61%, rgba(246,172,218,1) 100%)","width":"auto","fontSize":"14px","lineHeight":"42px","height":"42px"}'></el-button>
				<el-button @click="resetForm('ruleForm')" :style='{"cursor":"pointer","border":"0","padding":"0px 30px","margin":"0 10px 0 0","outline":"none","color":"#666","borderRadius":"6px","background":"#f0f0f0","width":"auto","fontSize":"14px","lineHeight":"42px","height":"42px"}'></el-button>
				<el-button @click="back()" :style='{"cursor":"pointer","border":"0","padding":"0px 30px","margin":"0 10px 0 0","outline":"none","color":"#666","borderRadius":"6px","background":"#f0f0f0","width":"auto","fontSize":"14px","lineHeight":"42px","height":"42px"}'></el-button>
			</el-form-item>
		</el-form>
	</div>
</div>
</template>

<script>
export default {
	data() {
		return {
			id: '',
			ruleForm: {
				shicaiName: '',
				quantity: 1,
				unit: '',
				purchaseDate: '',
				expiryDate: '',
				status: 'new'
			},
			rules: {
				shicaiName: [
					{ required: true, message: '', trigger: 'blur' }
				],
				quantity: [
					{ required: true, message: '', trigger: 'blur' }
				],
				unit: [
					{ required: true, message: '', trigger: 'change' }
				],
				purchaseDate: [
					{ required: true, message: '', trigger: 'change' }
				],
				expiryDate: [
					{ required: true, message: '', trigger: 'change' }
				],
				status: [
					{ required: true, message: '', trigger: 'change' }
				]
			},
			selectedShelfLife: null,
			shelfLifeList: []
		}
	},
	created() {
		this.id = this.$route.query.id;
		if (this.id) {
			this.getInfo();
		} else {
			// 
			this.ruleForm.purchaseDate = this.formatDateTime(new Date());
		}
		// 
		this.loadShelfLifeList();
	},
	methods: {
		// 
		loadShelfLifeList() {
			this.$http.get('shicaishelflife/list').then(res => {
				if (res.data && res.data.code === 0) {
					this.shelfLifeList = res.data.data || [];
				}
			});
		},
		// 
		queryShelfLife(queryString, cb) {
			let results = queryString ? 
				this.shelfLifeList.filter(item => 
					item.shicaiName.toLowerCase().includes(queryString.toLowerCase())
				) : this.shelfLifeList;
			
			//  value  autocomplete 
			results = results.map(item => ({
				...item,
				value: item.shicaiName  // autocomplete  value 
			}));
			
			cb(results);
		},
		// 
		handleShelfLifeSelect(item) {
			this.selectedShelfLife = item;
			this.$message.success(` ${item.shicaiName} ${item.shelfLifeDays} `);
		},
		// 
		applyShelfLife() {
			if (!this.selectedShelfLife) {
				this.$message.warning('');
				return;
			}
			if (!this.ruleForm.purchaseDate) {
				this.$message.warning('');
				return;
			}
			
			let purchaseDate = new Date(this.ruleForm.purchaseDate);
			let expiryDate = new Date(purchaseDate);
			expiryDate.setDate(expiryDate.getDate() + this.selectedShelfLife.shelfLifeDays);
			
			this.ruleForm.expiryDate = this.formatDateTime(expiryDate);
			this.$message.success('');
		},
		// 
		getInfo() {
			this.$http.get(`usershicai/info/${this.id}`).then(res => {
				if (res.data && res.data.code === 0) {
					this.ruleForm = res.data.data;
				}
			});
		},
		// 
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					let userid = this.$storage.get('userid');
					this.ruleForm.userid = userid;
					
					let url = this.id ? 'usershicai/update' : 'usershicai/save';
					this.$http.post(url, this.ruleForm).then(res => {
						if (res.data && res.data.code === 0) {
							this.$message.success(this.id ? '' : '');
							this.$router.push('/index/usershicaiList');
						} else {
							this.$message.error(res.data.msg);
						}
					});
				} else {
					return false;
				}
			});
		},
		// 
		resetForm(formName) {
			this.$refs[formName].resetFields();
		},
		// 
		back() {
			this.$router.go(-1);
		},
		// 
		formatDateTime(date) {
			let d = new Date(date);
			return d.getFullYear() + '-' + 
				   String(d.getMonth() + 1).padStart(2, '0') + '-' + 
				   String(d.getDate()).padStart(2, '0') + ' ' +
				   String(d.getHours()).padStart(2, '0') + ':' +
				   String(d.getMinutes()).padStart(2, '0') + ':' +
				   String(d.getSeconds()).padStart(2, '0');
		}
	}
}
</script>

<style scoped>
.el-form {
	max-width: 800px;
	margin: 0 auto;
}
.shelf-life-item {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.shelf-life-item .name {
	font-weight: bold;
}
.shelf-life-item .shelf-life {
	color: #909399;
	font-size: 12px;
}
</style>
