<template>
  <div class="preference-container">
    <div class="page-title"></div>
    
    <el-card class="preference-card">
      <div slot="header">
        <span></span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="loadPreference"></el-button>
      </div>

      <el-form :model="preferenceForm" :rules="rules" ref="preferenceForm" label-width="120px">
        <!--  -->
        <el-form-item label="" prop="dietType">
          <el-select v-model="preferenceForm.dietType" placeholder="" style="width: 300px">
            <el-option label="" value="normal"></el-option>
            <el-option label="" value="vegetarian"></el-option>
            <el-option label="" value="vegan"></el-option>
            <el-option label="" value="low-carb"></el-option>
            <el-option label="" value="low-fat"></el-option>
            <el-option label="" value="high-protein"></el-option>
          </el-select>
          <span class="tip"></span>
        </el-form-item>

        <!--  -->
        <el-form-item label="">
          <div class="nutrient-goals">
            <div class="nutrient-item">
              <span class="nutrient-label">/</span>
              <el-input-number v-model="preferenceForm.proteinGoal" :min="0" :max="300" style="width: 150px"></el-input-number>
            </div>
            <div class="nutrient-item">
              <span class="nutrient-label">/</span>
              <el-input-number v-model="preferenceForm.carbGoal" :min="0" :max="500" style="width: 150px"></el-input-number>
            </div>
            <div class="nutrient-item">
              <span class="nutrient-label">/</span>
              <el-input-number v-model="preferenceForm.fatGoal" :min="0" :max="200" style="width: 150px"></el-input-number>
            </div>
          </div>
          <span class="tip"></span>
        </el-form-item>

        <!--  -->
        <el-form-item label="" prop="flavor">
          <el-radio-group v-model="preferenceForm.flavor">
            <el-radio label="mild"></el-radio>
            <el-radio label="spicy"></el-radio>
            <el-radio label="sweet"></el-radio>
            <el-radio label="salty"></el-radio>
          </el-radio-group>
          <span class="tip"></span>
        </el-form-item>

        <!--  -->
        <el-form-item label="" prop="allergyInfo">
          <el-input
            type="textarea"
            v-model="preferenceForm.allergyInfo"
            placeholder=",,"
            :rows="3"
            style="width: 500px">
          </el-input>
          <span class="tip"></span>
        </el-form-item>

        <!--  -->
        <el-form-item>
          <el-button type="primary" @click="savePreference" :loading="saving"></el-button>
          <el-button @click="resetForm"></el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <!--  -->
    <el-card class="preview-card" v-if="currentPreference">
      <div slot="header">
        <span></span>
      </div>
      <el-descriptions :column="2" border>
        <el-descriptions-item label="">
          <el-tag>{{ getDietTypeText(currentPreference.dietType) }}</el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="">
          <el-tag type="success">{{ getFlavorText(currentPreference.flavor) }}</el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="">
          {{ currentPreference.proteinGoal || 0 }} /
        </el-descriptions-item>
        <el-descriptions-item label="">
          {{ currentPreference.carbGoal || 0 }} /
        </el-descriptions-item>
        <el-descriptions-item label="">
          {{ currentPreference.fatGoal || 0 }} /
        </el-descriptions-item>
        <el-descriptions-item label="">
          <el-tag v-if="!currentPreference.allergyInfo || currentPreference.allergyInfo === ''" type="info"></el-tag>
          <el-tag v-else type="warning">{{ currentPreference.allergyInfo }}</el-tag>
        </el-descriptions-item>
      </el-descriptions>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userId: localStorage.getItem('userid'),
      preferenceForm: {
        dietType: 'normal',
        proteinGoal: 50,
        carbGoal: 150,
        fatGoal: 40,
        flavor: 'mild',
        allergyInfo: ''
      },
      currentPreference: null,
      rules: {
        dietType: [
          { required: true, message: '', trigger: 'change' }
        ],
        flavor: [
          { required: true, message: '', trigger: 'change' }
        ]
      },
      saving: false
    }
  },
  created() {
    this.loadPreference();
  },
  methods: {
    // 
    loadPreference() {
      this.$http.get(`yonghu/preference/${this.userId}`).then(res => {
        if (res.data.code === 0 && res.data.data) {
          const data = res.data.data;
          
          // JSON
          if (data.healthPreference) {
            try {
              const healthPref = JSON.parse(data.healthPreference);
              this.preferenceForm.dietType = healthPref.dietType || 'normal';
              this.preferenceForm.flavor = healthPref.flavor || 'mild';
              
              if (healthPref.nutrientGoal) {
                this.preferenceForm.proteinGoal = healthPref.nutrientGoal.protein || 50;
                this.preferenceForm.carbGoal = healthPref.nutrientGoal.carb || 150;
                this.preferenceForm.fatGoal = healthPref.nutrientGoal.fat || 40;
              }
            } catch (e) {
              console.error(':', e);
            }
          }
          
          // 
          this.preferenceForm.allergyInfo = data.allergyInfo || '';
          
          // 
          this.currentPreference = { ...this.preferenceForm };
        }
      }).catch(err => {
        console.error(':', err);
      });
    },
    
    // 
    savePreference() {
      this.$refs.preferenceForm.validate((valid) => {
        if (valid) {
          this.saving = true;
          
          const requestData = {
            userId: this.userId,
            dietType: this.preferenceForm.dietType,
            proteinGoal: this.preferenceForm.proteinGoal,
            carbGoal: this.preferenceForm.carbGoal,
            fatGoal: this.preferenceForm.fatGoal,
            flavor: this.preferenceForm.flavor,
            allergyInfo: this.preferenceForm.allergyInfo || ''
          };
          
          this.$http.post('yonghu/preference/init', requestData).then(res => {
            this.saving = false;
            if (res.data.code === 0) {
              this.$message.success('');
              this.currentPreference = { ...this.preferenceForm };
              
              // 
              this.$http.get(`caipuxinxi/clearCache?userId=${this.userId}`).catch(() => {});
            } else {
              this.$message.error(res.data.msg || '');
            }
          }).catch(err => {
            this.saving = false;
            this.$message.error('' + err.message);
          });
        } else {
          this.$message.warning('');
          return false;
        }
      });
    },
    
    // 
    resetForm() {
      this.loadPreference();
    },
    
    // 
    getDietTypeText(type) {
      const map = {
        'normal': '',
        'vegetarian': '',
        'vegan': '',
        'low-carb': '',
        'low-fat': '',
        'high-protein': ''
      };
      return map[type] || type;
    },
    
    // 
    getFlavorText(flavor) {
      const map = {
        'mild': '',
        'spicy': '',
        'sweet': '',
        'salty': ''
      };
      return map[flavor] || flavor;
    }
  }
}
</script>

<style scoped>
.preference-container {
  padding: 20px;
  max-width: 1000px;
  margin: 0 auto;
}

.page-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #333;
}

.preference-card {
  margin-bottom: 20px;
}

.nutrient-goals {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.nutrient-item {
  display: flex;
  align-items: center;
}

.nutrient-label {
  width: 180px;
  color: #666;
  font-size: 14px;
}

.tip {
  display: block;
  margin-top: 8px;
  color: #999;
  font-size: 12px;
}

.preview-card {
  margin-top: 20px;
}
</style>
